import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { catchError, map, tap } from 'rxjs/operators';
import { MessageService } from '../message.service';
// import { Inventory } from '../models/inventory';

const httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable()
export class OrderService {

    private measuresUrl = 'http://localhost:3002/api/order';  // URL to web api

    constructor(
        private http: HttpClient, private messageService: MessageService) { }

    /** POST: add a new inventory to the server */
    placeOrder(order): Observable<any> {
        return this.http.post<any>(this.measuresUrl, order, httpOptions).pipe(
            tap((hero: any) => this.log(`added hero`)),
            catchError(this.handleError<any>('addHero'))
        );
    }

    private handleError<T>(operation = 'operation', result?: T) {
        return (error: any): Observable<T> => {

            // TODO: send the error to remote logging infrastructure
            console.error(error); // log to console instead

            // TODO: better job of transforming error for user consumption
            this.log(`${operation} failed: ${error.message}`);

            // Let the app keep running by returning an empty result.
            return of(result as T);
        };
    }

    private log(message: string) {
        this.messageService.add('InventoryService: ' + message);
    }

}


